import React from 'react';
import { Link } from "react-router-dom";

class Menu extends React.Component {
    render() {
        return (
            <>
                <div className="atorn-nav">
                    <div className="container">
                        <nav className="navbar navbar-expand-md navbar-light">
                            <a className="navbar-brand" href="index.html">
                                <img src="assets/img/logo.png" className="logo1" alt="logo" />
                                <img src="assets/img/logo-white.png" className="logo2" alt="logo" />
                            </a>

                            <div className="collapse navbar-collapse mean-menu">
                                <ul className="navbar-nav ms-auto">
                                    <li className="nav-item">
                                        <Link to="/" className="nav-link">
                                            Home
                                        </Link>
                                    </li>

                                    <li className="nav-item">
                                        <a href="#" className="nav-link active">
                                            About <i className="las la-angle-down"></i>
                                        </a>
                                        <ul className="dropdown-menu">
                                            <li className="nav-item"><Link to="/about" className="nav-link">About Us</Link></li>
                                            <li className="nav-item"><Link to="/contact" className="nav-link">Structure and Departments</Link></li>
                                            <li className="nav-item"><Link to="/contact" className="nav-link">Juridiction</Link></li>
                                        </ul>
                                    </li>



                                    <li className="nav-item">
                                        <a href="#" className="nav-link">
                                            Team Members <i className="las la-angle-down"></i>
                                        </a>
                                        <ul className="dropdown-menu">
                                            <li className="nav-item">
                                                <Link to="/team" className="nav-link">Judicial cadre</Link>
                                            </li>
                                            <li className="nav-item">
                                                <Link to="/team" className="nav-link">Management Staff</Link>
                                            </li>

                                            <li className="nav-item">
                                                <Link to="/team" className="nav-link">Past President</Link>
                                            </li>

                                            <li className="nav-item">
                                                <Link to="/team" className="nav-link">Past Judges</Link>
                                            </li>

                                            <li className="nav-item">
                                                <Link to="/team" className="nav-link">Past Chief Registrar</Link>
                                            </li>


                                        </ul>
                                    </li>

                                    <li className="nav-item">
                                        <a href="#" className="nav-link">
                                            Divisions <i className="las la-angle-down"></i>
                                        </a>
                                        <ul className="dropdown-menu">
                                            <li className="nav-item"><Link to="/divisions" className="nav-link">Court Divisions</Link></li>
                                            <li className="nav-item"><Link to="/" className="nav-link">Court Registry</Link></li>
                                        </ul>
                                    </li>

                                    <li className="nav-item">
                                        <a href="#" className="nav-link">
                                            Litigation  <i className="las la-angle-down"></i>
                                        </a>
                                        <ul className="dropdown-menu">
                                            <li className="nav-item"><Link to="/judgement" className="nav-link">Judgement</Link></li>
                                            <li className="nav-item"><Link to="/case-file" className="nav-link">File case</Link></li>
                                            <li className="nav-item"><Link to="/cause-list" className="nav-link">Cause List</Link></li>
                                        </ul>
                                    </li>

                                    <li className="nav-item">
                                        <a href="#" className="nav-link">
                                            Media <i className="las la-angle-down"></i>
                                        </a>
                                        <ul className="dropdown-menu">
                                            <li className="nav-item"><Link to="/news" className="nav-link">News and Events</Link></li>
                                            <li className="nav-item"><Link to="/gallery" className="nav-link">Picture Gallery</Link></li>
                                            <li className="nav-item"><Link to="/videos" className="nav-link">Videos</Link></li>
                                        </ul>
                                    </li>

                                    <li className="nav-item">
                                        <a href="#" className="nav-link">
                                            Contact <i className="las la-angle-down"></i>
                                        </a>
                                        <ul className="dropdown-menu">
                                            <li className="nav-item"><Link to="/contact" className="nav-link">Contact Us</Link></li>
                                            <li className="nav-item"><Link to="/faq" className="nav-link">FAQ</Link></li>
                                            <li className="nav-item"><Link to="/help" className="nav-link">Help</Link></li>
                                        </ul>
                                    </li>
                                </ul>
                            </div>
                        </nav>
                    </div>
                </div>
            </>
        );
    }
}

export { Menu };