import React,{ lazy, Suspense } from 'react';
import ReactDOM from 'react-dom/client';
import { BrowserRouter, Routes, Route } from "react-router-dom";
import App from './App';
import CaseFile from './pages/caseFile';
import CauseList from './pages/causeList';
import Help from './pages/help';
import FAQ from './pages/faq';
import Terms from './pages/terms';
import Privacy from './pages/privacy';



const About= React.lazy(()=>import('./pages/about'));
const Contact= React.lazy(()=>import('./pages/contact'));
const Page= React.lazy(()=>import('./pages/page'));
const News= React.lazy(()=>import('./pages/news'));
const Judgement= React.lazy(()=>import('./pages/judgement'));
const NewsDetails= React.lazy(()=>import('./pages/newsDetails'));
const Team= React.lazy(()=>import('./pages/team'));
const PictureGallery= React.lazy(()=>import('./pages/pictureGallery'));
const Videos= React.lazy(()=>import('./pages/videos'));
const Divisions= React.lazy(()=>import('./pages/divisions'));

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <BrowserRouter>
    <Suspense fallback={<>loading...</>}>
        <Routes>
          <Route path="/" element={<App />} exact />
          <Route path="/index.html" element={<App />} exact />
          <Route path="/about" element={<About />} />
          <Route path="/contact" element={<Contact />} />
          <Route path="/page" element={<Page />} />
          <Route path="/news" element={<News />} />
          <Route path="/news.html" element={<News />} />
          <Route path="/news-details" element={<NewsDetails />} />
          <Route path="/videos" element={<Videos />} />
          <Route path="/gallery" element={<PictureGallery />} />
          <Route path="/cause-list" element={<CauseList />} />
          <Route path="/case-file" element={<CaseFile />} />
          <Route path="/help" element={<Help />} />
          <Route path="/faq" element={<FAQ />} />
          <Route path="/faq.html" element={<FAQ />} />
          <Route path="/blog-details.html" element={<NewsDetails />} />
          <Route path="/team.html" element={<Team />} />
          <Route path="/team" element={<Team />} />
          <Route path="/terms" element={<Terms />} />
          <Route path="/privacy" element={<Privacy />} />
          <Route path="/judgement" element={<Judgement />} />
          <Route path='/divisions' element={<Divisions/>}/>
        </Routes>
        </Suspense>
      </BrowserRouter>
  </React.StrictMode>
);
